.ref-area-main-div{
    background-color: white;
    justify-content: center;
    display: flex;
    padding-block: 50px;
    padding-inline: 16%;

    @media (max-width:990px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    @media (max-width:1200px) {
        padding-inline: 0;
    }

}

.perspektif{
    padding-inline: 50px;
    align-items: center;
    display: flex;
    flex-direction: column;
    padding: 30px;
    @media (max-width:990px) {
        border: none;
        padding-right: 30px;
    }
}
.divider{
    justify-content: center;
    display: flex;
    border-top: 3px solid #2c75c9;
    width: 100px;
}
.perspektif p{
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    color: #353535;
    text-transform: none;
    font-size: 18px;
    padding-top: 20px;
}
.perspektif h1{
    justify-content: center;
    display: flex;
    font-family: 'Montserrat', sans-serif;
    color: #535353;
    padding-bottom: 10px;
    font-size: 30px;
    font-weight: bold;
}
.logo-ref{
    width: 200px;
    height: auto;
}
.perspektif h3{
    justify-content: center;
    display: flex;
    font-family: 'Montserrat', sans-serif;
    color: #3383c6;
    font-size: 19px;
    padding-bottom: 0px;
}

.ref-ar{
    display: none;
    padding: 30px;
    
}

.ref-ar h3{
    
    color: #535353;
    padding-bottom: 10px;
    border-bottom: 2px solid #3383c6;
    font-size: 30px;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
}

button{
    background-color: rgb(44, 117, 201); /* Green */
    border: 3px solid rgb(44, 117, 201); 
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

.grid{
    padding-top: 20px;
    height: auto;
    width: auto;
    display: block;

    @media (min-width:768px) {
        display: grid;
        gap: 5px;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 120px 120px 120px;
    }

    .col{
        width: 10%;
        display: flex;
        background-color: rgb(255, 255, 255);

        .img{
            display: flex;
            width: auto;
            
        }
        
    }
    
}