footer .container{
    background-color: rgb(247 247 247);
    padding-inline: 16%;
    display: flex;
    justify-content: space-between;
    padding-block: 50px;
}

.sec-links{
    width: 50%;
}

footer .container div ul{

    list-style: none;

}

.sec-about-us{
    width: 30%;
}

.sec-about-us p{
    text-align: justify;
    padding-bottom: 10px;
}

.sec-about-us p{
    text-align: justify;
    padding-bottom: 10px;
}

.sec-about-us h2{
    font-weight: 300;
    padding-bottom: 20px;
}

iframe{
    outline: 0;
    border: 0;
}

.bottom-copyrights-area{
    display: flex;
    justify-content: center;
    padding-block: 10px;
    background-color: white;
}

.bottom-copyrights-area p{
 font-size: 15px;
}

@media(max-width:991px){
    footer .container{
        flex-direction: column;
    }
    .sec-about-us{
        width: 100%;
    }
    iframe{
        width: 80%;
        height: 150px;
    }

    .sec-links{
        padding-top: 20px;
        display: flex;
        justify-content: center;
        width: 100%;
        
    }
}

@media(max-width:1500px) and (min-width:991px){
  
    iframe{
        width: 440px;
        height: 330px;
    }

    .sec-links{
        justify-content: center;
    display: flex;
    align-items: center;
    }

}